const HomeSideBar = () => {
  const users = [
    {
      id: "1",
      username: "hello.world",
      desc: "New to instagram"
    },
    {
      id: "2",
      username: "nmastay_london",
      desc: "New to instagram"
    },
    {
      id: "3",
      username: "peter_parker",
      desc: "Follows You"
    },
    {
      id: "2",
      username: "unknown.person",
      desc: "working with you"
    }
  ];
  return (
    <>
      <div className="suggestions">
        <div className="suggestions-heading">suggestions for you</div>
        {users.map((item, index) => (
          <div className="suggestion-div">
            <div>
              <div className="profile-pic">P</div>
            </div>
            <div className="message-text">
              <span>{item.username}</span>
              <span className="message-status" style={{ marginTop: "4px" }}>
                {item.desc}
              </span>
            </div>
            <p className="suggestion-follow-btn">Follow</p>
          </div>
        ))}
      </div>
    </>
  );
};

export default HomeSideBar;
